@import '../abstracts/mixin.scss';
@import '../abstracts/variables.scss';

.page {
	font-size: 1.6rem;
	height: 90%;
	width: 90%;
	display: grid;
	grid-template-columns: 20rem 3fr;
	grid-template-rows: 1fr 4rem;
	column-gap: 2rem;

    

	.message {
        grid-column: 2 / 3;
        grid-row: 1 / 2;
        width: 100%;
        // height: 48rem;
        height: 98%;
        bottom: 0rem;
        padding: 1rem;
        background-color: $almost-white;
        border: 0.2rem solid black;
        overflow: auto !important;
        overscroll-behavior: revert;
        // overflow-x: hidden !important;
        display: flex;
        flex-direction: column-reverse;
        // justify-content: flex-end;
        // bottom: -3rem;
        font-size: small;
        @include shadowBox();
    }

    .input {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
        height: 100%;

        .sendInput {
            @include shadowBox();
            border: 0.2rem solid black;
            // margin-top: 2rem;
            height: 4rem;
            width: 100%;
            padding: 0.5rem;
            
            &:focus {
                outline: none;
            }
        }
    }

