	// 	&:focus {
	// 		outline: none;
	// 	}
	// }

	.conversations {
		width: 100%;
		max-height: 60rem;
		grid-column: 1 / 2;
		grid-row: 1 / 3;
		background-color: $almost-white;
		border: 0.2rem solid black;
        overflow: scroll;
		overflow-x: hidden;
		display: flex;
		flex-direction: column;
		gap: 1rem;
		padding-top: 1rem;
		
        @include shadowBox();
	}
	
}
