@import '../../abstracts/mixin.scss';
@import '../../abstracts/variables.scss';

.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 10;
	background: rgba(0, 0, 0, 0.75);
  }
  
	.modal {
		position: fixed;
		top: 10vh;
		left: 10%;
		width: 50rem;
		z-index: 100;
		overflow: hidden;
		background: white;
		display: flex;
		max-height: 70rem;
		overflow: scroll;
		overflow-x: hidden;

	.header {
		padding: 1rem;
		padding-left: 1.4rem;
	}

	h1 {
		width: 100%;
		text-align: center;
		font-size: 3rem;
		font-weight: 800;
		letter-spacing: -0.15rem;
		padding: 0.5rem 1rem;

		color: $almost-white;

		border: 0.2rem solid black;
		border-radius: none;

		position:relative;
		transform-style: preserve-3d;

		&::before {
			content: '';
			position: absolute;
			height: 100%;
			width: 100%;
			transform: translateZ(-0.1rem);
			z-index: -1;
			bottom: -0.8rem;
			left: -0.8rem;
			background-color: $light-vermillon;
			opacity: 1;
			transition: all 0.3s;
		}
	}

	h2 {
		font-size: 1.3rem;
		font-weight: 800;
		padding: 0.5rem 1rem;
	}

	.content {
		font-size: 1.5rem;
		padding: 1rem;
		}

	.actions {
		padding: 1rem;
		display: flex;
		justify-content: space-between;
		width: 100%;
	}

	.error {
		width: 80%;
		font-size: 1.8rem;
		padding: 1rem;
		font-weight: 700;
		padding: 1rem;
		padding-left: 1.4rem;
		text-align: center;

	}

}
	.button {
		@include buttonMixin();
		
	}

	.button-cancel {
		@include buttonMixin($color: $gunmetal-dark);
		
	}

  
  @media (min-width: 768px) {
	.modal {
	  left: calc(50% - 20rem);
	  width: 40rem;
	}
  }